import React from 'react';
import { Helmet } from 'react-helmet-async';

// Obtener BASE_URL - en desarrollo es '/', en produccin GitHub Pages es '/dreieck-consultora/'
const BASE_URL = import.meta.env.BASE_URL || '/';

interface SeoHeadProps {
  title?: string;
  description?: string;
  keywords?: string;
  image?: string;
  url?: string;
  type?: 'website' | 'article';
  siteName?: string;
  locale?: string;
}

export const SeoHead: React.FC<SeoHeadProps> = ({
  title = 'Dreieck - Consultora y Soluciones de Business Intelligence',
  description = 'Transformamos datos en decisiones estratgicas para tu negocio. Especialistas en Power BI, anlisis de datos y consultora empresarial.',
  keywords = 'consultora, business intelligence, power bi, anlisis de datos, dashboard, visualizacin de datos, empresa de anlisis de datos',
  image = 'https://dreieck-consultora.netlify.app/og-image.png',
  url = 'https://dreieck-consultora.netlify.app',
  type = 'website',
  siteName = 'Dreieck',
  locale = 'es_ES',
}) => {
  return (
    <Helmet>
      <title>{title}</title>
      <meta name="description" content={description} />
      <meta name="keywords" content={keywords} />
      <meta name="robots" content="index, follow" />
      <meta name="viewport" content="width=device-width, initial-scale=1.0" />

      {/* Open Graph / Facebook */}
      <meta property="og:type" content={type} />
      <meta property="og:url" content={url} />
      <meta property="og:title" content={title} />
      <meta property="og:description" content={description} />
      <meta property="og:image" content={image} />
      <meta property="og:site_name" content={siteName} />
      <meta property="og:locale" content={locale} />

      {/* Twitter */}
      <meta name="twitter:card" content="summary_large_image" />
      <meta name="twitter:url" content={url} />
      <meta name="twitter:title" content={title} />
      <meta name="twitter:description" content={description} />
      <meta name="twitter:image" content={image} />

      {/* Favicon */}
      <link rel="icon" href={`${BASE_URL}favicon.ico`} />
      <link rel="apple-touch-icon" sizes="180x180" href={`${BASE_URL}apple-touch-icon.png`} />
      <link rel="icon" type="image/png" sizes="32x32" href={`${BASE_URL}favicon-32x32.png`} />
      <link rel="icon" type="image/png" sizes="16x16" href={`${BASE_URL}favicon-16x16.png`} />
      <link rel="manifest" href={`${BASE_URL}site.webmanifest`} />

      {/* Canonical URL */}
      <link rel="canonical" href={url} />
    </Helmet>
  );
};

export default SeoHead;